<template>
<v-container fluid class="down-top-padding">
    <v-row>
      <v-col cols="12" lg="6">
        <v-card
    max-width="500"
    class="mx-auto"
  >
    <v-toolbar
      color="deep-purple accent-4"
      dark
    >
       <v-btn icon @click="messages=[];selectTeacher=true">
        <v-icon>mdi-arrow-left</v-icon>
      </v-btn>

      <v-toolbar-title>Mesajlar</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn icon>
        <v-icon>mdi-message</v-icon>
      </v-btn>
    </v-toolbar>

    <v-list subheader v-if="selectTeacher">
      <v-subheader>Recent chat</v-subheader>
       <v-text-field
       :loading="loading"
       style="padding:20px"
       hint="Öğretmen Adı veya Soyadı arayabilirsiniz."
            label="Arama"
            v-model="searchingTeacher"
          ></v-text-field>
      <v-list-item
        v-for="item in items"
        :key="item.title"
       @click="setTeacherChats(item)"
      >
        <v-list-item-avatar>
          <v-img :src="item.image==0?'https://s3.amazonaws.com/37assets/svn/765-default-avatar.png':item.image"></v-img>
        </v-list-item-avatar>

        <v-list-item-content>
          <v-list-item-title v-text="item.fullName"></v-list-item-title>
        </v-list-item-content>

        <v-list-item-icon>
          <v-icon :color="'deep-purple accent-4'">mdi-message</v-icon>
        </v-list-item-icon>
      </v-list-item>
        <div  v-if="loading">
          <v-skeleton-loader
        v-for="item in 8" :key="item"
        ref="skeleton"
        :boilerplate="boilerplate"
        :type="type"
        :tile="tile"
        class="mx-auto"
      ></v-skeleton-loader>
        </div>
           <p v-if="items.length==0 && !loading" class="text-center">Öğretmen Bulamadık.</p>
    </v-list>
    <v-list subheader v-else>
      <v-subheader>Previous chats</v-subheader>

      <v-list-item
        v-for="item in items2"
        :key="item.title"
        @click="setMessages(item)"
      >
        <v-list-item-avatar>
         <v-img :src="item.image==0?'https://s3.amazonaws.com/37assets/svn/765-default-avatar.png':item.image"></v-img>
        </v-list-item-avatar>
        <v-list-item-content>
          <v-list-item-title v-text="item.fullName"></v-list-item-title>
           <v-list-item-subtitle>{{item.content}}</v-list-item-subtitle>
        </v-list-item-content>
           <v-list-item-action>
           <v-list-item-action-text v-text="item.lastSeen"></v-list-item-action-text>
           <v-icon
                  color="grey lighten-1"
                >
                 mdi-message-outline
                </v-icon>
           </v-list-item-action>
      </v-list-item>
      <p v-if="items2.length==0" class="text-center">Mesaj Kutusu Boş</p>
    </v-list>
  </v-card>
      </v-col>
      <v-col cols="12" lg="4" style="overflow-y:scroll; overflow-x:hidden; height:600px;">
       <v-card v-if="messages.length!=0">
          <v-card-text>
            Mesajlar
          </v-card-text>
          <v-divider></v-divider>
          <v-card-text style="background:#ece5dd">
         <ul v-for="(item,index) in messages" :key="index" style="padding:0">
           <li  :class="item.senderId==activeTeacher?'float-right chat_right':'chat_left float-left'"><div><span>{{item.content}}</span></div>
           </li>
           <br><br>
             <p style="text-decoration:underline" :class="item.senderId==activeTeacher?' text-right':'text-left '">{{item.atDate}}</p>
         </ul>

           <div >

           </div>
           </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
data(){
  return{
    selectTeacher:true,
    loading:false,
    searchingTeacher:"",
    activeTeacher:null,
     boilerplate: false,
      tile: false,
      type: 'list-item-avatar-three-line',
      types: [],
     items: [
      ],
      items2: [
      ],
      messages:[]
  }
},
watch:{
  searchingTeacher(data){
    if(data.length>3){
      this.$api.get('user?filter=isTeacher=1&searchFields=name,lastName&search='+data).then(res=>{
    if(res.count!=0){
      res.body.forEach(pr=>{
        pr.image = pr.image.replace('public/',this.$api.BASE_API_URL)
        pr.fullName = pr.name + ' ' + pr.lastName
      })
      this.items = res.body
    }
    else{
      this.items=[]
    }
  })
    }
    else if(data.length==0){
      this.initTeacher()
    }
  }
},
methods:{
  setTeacherChats(item){
    this.activeTeacher = item.id
    this.$api.get('messageBox/'+item.id).then(res=>{
      if(res.count!=0){
        res.body.forEach(fe=>{
        fe.lastSeen = this.$settings.dataFormatterNaApiWithTime(fe.lastSeen)
        fe.image = fe.image.replace('public/',this.$api.BASE_API_URL)
          fe.fullName = fe.name + ' '+ fe.lastname
        })
        this.items2 = res.body
        this.selectTeacher = false
      }
      else{
        this.items2 = []
        this.selectTeacher = false
      }
    })
  },
  initTeacher(){
    this.loading =true
    this.$api.get('user?filter=isTeacher=1').then(res=>{
    if(res.count!=0){
      res.body.forEach(pr=>{
        pr.image = pr.image.replace('public/',this.$api.BASE_API_URL)
        pr.fullName = pr.name + ' ' + pr.lastName
      })
      this.items = res.body
          this.loading =false
    }
    else{
      this.items=[]
          this.loading =false
    }
  })
  },
  setMessages(row){
    this.messages=[]
    this.$api.get(`i/j/w/messages/user?on=messages.receiverId=t2.id&filter=messages.receiverId=${row.receiverId},messages.senderId=${row.senderId}||messages.receiverId=${row.senderId},messages.senderId=${row.receiverId}&fields=messages.*,t2.name,t2.lastName,t2.image&limit=200`).then(res=>{
      if(res.status=='success'){
        res.body.forEach(pr=>{
        pr.atDate = this.$settings.dataFormatterNaApiWithTime(pr.atDate)
                pr.image = pr.image.replace('public/',this.$api.BASE_API_URL)

        })
        this.messages = res.body
      }
      else{
        this.messages = []
      }

    })
  }
},
mounted(){
  this.initTeacher()
}
}
</script>

